html{
  font-size: 62.5%;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.top-menu-shadow{
  box-shadow: 0px 4px 16px 0px #C7C7C770;
}

.icon {
  overflow: hidden;
  cursor: pointer;
}
.icon:after {
  content: "";
  height: 200%;
  left: -230%;
  opacity: 0;
  position: absolute;
  top: -20%;
  transform: rotate(-30deg);
  width: 200%;
  background: rgba(247, 241, 241, 0.55);
  background: linear-gradient(to right, rgba(247, 237, 237, 0.33) 0%, rgba(255, 255, 255, 0.33) 77%, rgba(255, 255, 255, 0.5) 92%, rgba(255, 254, 254, 0) 100%);
}
/* Hover state - trigger effect */

.icon:hover:after {
  left: -50%;
  opacity: 1;
  top: -50%;
  transition-duration: 0.7s, 0.7s, 0.15s;
  transition-property: left, top, opacity;
  transition-timing-function: ease;
}
/* Active state */

.icon:active:after {
  opacity: 0;
}

